import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {Http} from "@angular/http";
import {LinkManagerProvider} from "../../providers/link-manager/link-manager";

/**
 * Generated class for the PrivacyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-privacy',
  templateUrl: 'privacy.html',
})
export class PrivacyPage {

  private jsonContent = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private http:Http, private linkManager: LinkManagerProvider) {
      this.http.get(this.linkManager.getDatenschutzAsJSON()).subscribe(response => {
        this.jsonContent = JSON.parse(response.text())["body"][0]["value"];

      })
  }

  ionViewDidLoad() {

  }

}



